import React, {useState, forwardRef, useEffect} from 'react'
import {Avatar} from '@material-ui/core'

import RetweetModal from '../retweetmodal/RetweetModal'
import Moment from 'react-moment'
import FooterIcon from './FooterIcon'
import Reply from '../Reply/Reply'
import Like from './Like'
import Popover from '@material-ui/core/Popover'
import Modal from '../../elements/Modal/Modal'
import util from '../../helpers/timeDifference'
import {convertTimestampToLocaleString} from '../../helpers/convertTimestampToLocaleString'

import VerifiedUserIcon from '@material-ui/icons/VerifiedUser'
import ChatBubbleOutlineIcon from '@material-ui/icons/ChatBubbleOutline'
import RepeatIcon from '@material-ui/icons/Repeat'
import PublishIcon from '@material-ui/icons/Publish'
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline'
import BarChartIcon from '@material-ui/icons/BarChart'
import CodeIcon from '@material-ui/icons/Code'
import PlaceIcon from '@material-ui/icons/Place'
import SentimentVeryDissatisfiedIcon from '@material-ui/icons/SentimentVeryDissatisfied'
import BlockIcon from '@material-ui/icons/Block'
import PostAddIcon from '@material-ui/icons/PostAdd'
import PersonAddDisabledIcon from '@material-ui/icons/PersonAddDisabled'
import PersonAddIcon from '@material-ui/icons/PersonAdd'
import CloseIcon from '@material-ui/icons/Close'
import { useHistory } from "react-router-dom";

import './Post.css'
import { db} from "../../firebase/firebase";
import { collection, orderBy, query, onSnapshot,updateDoc,  doc } from 'firebase/firestore';

import {useStateValue} from '../../contexts/StateContextProvider'
import {like, unlike, follow, unfollow, deletePost,retweet} from '../../server/serverActions'
import { Button, Dialog, DialogActions, DialogContent, TextareaAutosize, DialogTitle } from '@material-ui/core';


const Post = forwardRef(({
   altText,
   text,
   image,
   createdAt,
   senderId,
   postId,
   itretweet,
   retweets,
   likes
   }, ref) => {

   const date = convertTimestampToLocaleString(createdAt)
   const history = useHistory();

   const [anchorEl, setAnchorEl] = useState(null)
   const onClickExpand= (event) => setAnchorEl(event.currentTarget)
   const handleClose = () => setAnchorEl(null)
   const open = Boolean(anchorEl)
   const id = open ? 'post-popover' : undefined
   const [isOpenModal, setIsOpenModal] = useState(false)
   const [OpenModal, setOpenModal] = useState(false)

   const [{user}] = useStateValue()
   const [profile, setProfile] = useState({id:'',displayName:'', photoURL: '', verified: false, username: '', followers:[], following:[]})
   const [ownProfile, setOwnProfile] = useState(null)
   const {displayName, username, photoURL, verified} = profile

   const [comments, setComments] = useState([]);
   const [isFollowing, setIsFollowing] = useState(false)
   

   const [noctuaEditMessage, setNoctuaEditMessage] = useState("");
// start edit modal
const [openEditModal, setOpenEditModal] = React.useState(false);
const handleOpenEditModal = () => {
  setOpenEditModal(true);
  handleClose();
}
const handleCloseEditModal = () => {
  setOpenEditModal(false);
  handleClose();
}
const handleEdit = () => {
  editPost(postId)
  handleCloseEditModal()
}
// end edit modal
// start edit function 
const editPost = async (postId) => {  
  const docRef = doc(db, 'posts', postId)
  const payload = {
      text: noctuaEditMessage
  }
  await updateDoc(docRef, payload)
}
// end edit function
useEffect(() => {
   const postsCollectionRef = collection(db, 'posts', postId,"comments")
   const q = query(postsCollectionRef, orderBy('createdAt', 'desc'))

   const unsub = onSnapshot(q, (snapshot) => {
       setComments(snapshot.docs.map(doc => ({...doc.data(), id: doc.id})) )
   })
   return unsub
}, [db,postId])

   useEffect(() => {
      onSnapshot(doc(db, "users", user.id), 
         snapshot=>{
            setOwnProfile({id:user.id, ...snapshot.data()}) 
     })
     
     onSnapshot(doc(db, "users",  senderId), 
     snapshot=>{
     setProfile(snapshot.data())
      
   })

   }, [])

   useEffect(() => {
      if(profile){
         setIsFollowing(profile.followers.includes(user.id))
      }
   }, [profile])

   const callbackForModal = () => {}

   return (
      <>
         <Modal  
            open={isOpenModal} 
            onClose={()=>setIsOpenModal(false)}
            title=""
            callback = {callbackForModal}
            Icon = {CloseIcon}
            ButtonText=''
         >
            <Reply props={{
                  altText,
                  text,
                  image,
                  createdAt,
                  senderId,
                  postId,
                  likes
               }}
               profile={profile}
               ownProfile ={ownProfile}
            />
         </Modal>
         <Modal  
            open={OpenModal} 
            onClose={()=>setOpenModal(false)}
            title=""
            callback = {callbackForModal}
            Icon = {CloseIcon}
            ButtonText=''
         >
             <RetweetModal props={{
                  altText,
                  text,
                  image,
                  createdAt,
                  senderId,
                  postId,
                  likes
               }}
               profile={profile}
               ownProfile ={ownProfile}
            />
            
         </Modal>
        

         <div className='post' ref={ref}>
            <div className="post__avatar">
               <Avatar src={photoURL} />
            </div>
            <div className="post__body">
               <div className="post__header">
                  <div className="post__headerText">
                     <h3>{displayName} {' '}
                        <span className='post__headerSpecial'> 
                              {verified && <VerifiedUserIcon className='post__badge'/>} 
                              @{`${username}`}{''}{' '}
                              <small>
                                 <Moment fromNow>{createdAt.toDate()}</Moment>
                              </small>
                        </span>
                     </h3>
                     <div className="post__headerExpandIcon" aria-describedby={id} variant="contained" onClick={onClickExpand } >
                        < MoreHorizIcon  />
                     </div>

                     <Popover 
                        id={id}
                              open={open}
                              anchorEl={anchorEl}
                              onClose={handleClose}

                        anchorOrigin={{
                           vertical: 'top',
                           horizontal: 'right',
                        }}
                        transformOrigin={{
                           vertical: 'top',
                           horizontal: 'right',
                        }}
                     >
                        <ul className="post__expandList">
                        {
                           senderId === user.id?
                           <>
                              <li onClick={()=>deletePost(postId)}>
                                 <div className='delete'><DeleteOutlineIcon /></div><h3 className="delete">Delete</h3>
                              </li>
                              <li>
                                 <div><PlaceIcon /></div><h3>Pin to your profile</h3>
                              </li>
                              <li onClick={handleOpenEditModal}>
                                 <div><CodeIcon /></div><h3>edit noctua</h3>
                              </li>
                              <li>
                                 <div><BarChartIcon /></div><h3>View noctua activity</h3>
                              </li>
                           </>
                           :
                           <>
                              <li>
                                 <div><SentimentVeryDissatisfiedIcon /></div><h3>Not interested in this noctua</h3>
                              </li>
                              {
                                 isFollowing?
                                    <li onClick={()=>unfollow(user.id, senderId)}>
                                       <div><PersonAddDisabledIcon /></div><h3>Unfollow {`@${username}`}</h3>
                                    </li>
                                 :  <li onClick={()=>follow(user.id, senderId)}>
                                       <div><PersonAddIcon /></div><h3>Follow {`@${username}`}</h3>
                                    </li>
                              }
                              <li>
                                 <div><PostAddIcon /></div><h3>Add/remove from Lists</h3>
                              </li>
                              <li>
                                 <div><BlockIcon /></div><h3>Block {`@${username}`}</h3>
                              </li>
                              <li>
                                 <div><CodeIcon /></div><h3>Embed Noctua</h3>
                              </li>
                           </>
                        }
                        </ul>
                     </Popover>
                     {/* Start Edit Modal*/}
            <div>
                <Dialog open={openEditModal} onClose={handleCloseEditModal}>
                  <DialogTitle>Edit Post</DialogTitle>
                  <DialogContent>
                  <TextareaAutosize
                    aria-label="minimum height"
                    minRows={3}
                    placeholder="What's happing..."
                    style={{ width: 200 }}
                    onChange={e=> setNoctuaEditMessage(e.target.value)}
                  />
                  </DialogContent>
                  <DialogActions>
                    <Button onClick={handleCloseEditModal}>Cancel</Button>
                    <Button onClick={handleEdit}>Edit</Button>
                  </DialogActions>
                </Dialog>
              </div>
            {/* End Edit Modal*/}
                  </div>
                  <div className="post__headerDescription" onClick={() =>
                history.push(`/post/${postId}/${senderId}`)}>
                      <p>{text}</p>
                      <img src={image} alt={altText} onClick='' />
                  </div>
               
               </div>

               <div className="post__footer">
               <FooterIcon Icon={ChatBubbleOutlineIcon} onClick={()=>setIsOpenModal(true)} value={comments.length} />
                  <FooterIcon Icon={RepeatIcon} value={retweets}  onClick={()=>setOpenModal(true)}/>
                  <Like 
                        likes={likes}
                        unlikeAction = {()=>unlike(postId, user.id)}
                        likeAction = {()=>like(postId, user.id,senderId)}
                  />
                  <FooterIcon Icon={PublishIcon} value={0}/>
               </div>
            </div>

         </div>
      </>
   )
  }
)

export default Post
